li.newest-item {
  display: flex;
  align-items: center;
}
li.newest-item .index {
  margin-left: 10px;
}
li.newest-item:nth-child(1) .index,
li.newest-item:nth-child(2) .index,
li.newest-item:nth-child(3) .index {
  color: red;
}
li.newest-item .info {
  flex: 1;
  display: flex;
  padding: 10px 0;
  border-bottom: 1px solid lightgray;
  padding-right: 10px;
  margin-left: 10px;
}
li.newest-item .info .detail {
  flex: 1;
}
li.newest-item .info .detail p {
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
  font-size: 12px;
  color: #888;
}
li.newest-item .info .detail p .artist::after {
  content: "/";
}
li.newest-item .info .detail p span:last-of-type::after {
  content: "";
}
li.newest-item .icon {
  width: 22px;
  height: 22px;
  background-image: url("../assets/index_icon_2x.png");
  background-size: 166px 97px;
  background-position: -24px 0;
}
