.m-input {
  padding: 15px 10px;
  position: relative;
}
.m-input::after {
  position: absolute;
  z-index: 2;
  content: "";
  top: 0px;
  left: 0;
  pointer-events: none;
  box-sizing: border-box;
  transform-origin: top left;
  border: 0 solid rgba(0, 0, 0, 0.1);
  border-bottom-width: 1px;
  width: 200%;
  height: 200%;
  transform: scale(0.5);
}
.m-input .search {
  position: relative;
  width: 100%;
  height: 30px;
  padding: 0 30px;
  box-sizing: border-box;
  background: #ebecec;
  border-radius: 30px;
  margin: 10px auto;
}
.m-input .search input {
  width: 100%;
  height: 30px;
  line-height: 18px;
  background: rgba(0, 0, 0, 0);
  font-size: 14px;
  color: #333;
  border: 0;
  outline: 0;
  line-height: 30px;
}
.m-input .search .searchicon {
  position: absolute;
  left: 0;
  top: 8px;
  margin: 0 10px;
  vertical-align: middle;
  width: 13px;
  height: 13px;
  background-image: url(../assets/1.png);
  background-size: contain;
  color: #ccc;
}
.m-input .close {
  position: absolute;
  right: 20px;
  top: 23px;
  width: 30px;
  height: 30px;
  line-height: 28px;
  text-align: center;
}
.m-input .close .u-svg {
  position: absolute;
  left: 10px;
  top: 10px;
  display: none;
  width: 14px;
  height: 14px;
  background-image: url(../assets/6.png);
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
}
