@keyframes mymove {
  from {
    background-position-x: -100%;
  }
  to {
    background-position-x: -200%;
  }
}
.lyric {
  overflow: scroll;
}
.lyric ul {
  margin: 0 30px;
  transition: 0.3s all;
}
.lyric ul li {
  margin-bottom: 10px;
  text-align: center;
}
.lyric ul li.active {
  font-weight: bold;
}
.lyric ul li.active span {
  background: linear-gradient(to right, #eb1717 50%, #fff 55%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: 200%;
  background-position-x: -100%;
  animation: mymove linear;
}
.lyric ul li.paused span {
  animation-play-state: paused;
}
