

























































@keyframes mymove {
  from {
    background-position-x: -100%;
  }
  to {
    background-position-x: -200%;
  }
}
.lyric {
  overflow: scroll;
  ul {
    margin: 0 30px;
    transition: 0.3s all;
    // transform: translateY(-100px);
    li {
      margin-bottom: 10px;
      text-align: center;
      &.active {
        font-weight: bold;
        span {
          background: linear-gradient(to right, #eb1717 50%, #fff 55%);
          background-clip: text;
          -webkit-text-fill-color: transparent;
          background-size: 200%;
          background-position-x: -100%;
          animation: mymove linear;
        }
      }
      &.paused{
        span{
          animation-play-state: paused;
        }
      }
    }
  }
}
