






























.playlist-card {
  .thumb {
    position: relative;
    top: 0;
    left: 0;
    img {
      width: 100%;
    }
    span {
      position: absolute;
      top: 5px;
      right: 5px;
      color: white;
      font-size: 12px;
      line-height: 1em;
      &::before {
        content: "";
        width: 1em;
        height: 1em;
        display: inline-block;
        background: url("data:image/svg+xml;base64,");
        background-repeat: no-repeat;
        vertical-align: bottom;
        margin-right: 3px;
      }
    }
  }
}
h4 {
  margin: 5px 0 10px 0;
  font-size: 13px;
  padding: 0 5px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
  text-align: justify;
  word-break: break-all;
  line-height: 1.2em;
}
