


































































.play-control {
  margin-top: 20px;
  padding: 0 20px;
  .progerss {
    width: 100%;
    height: 10px;
    // background: lightcoral;

    position: relative;
    display: flex;
    align-items: center;

    input {
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      z-index: 1;
      opacity: 0;
    }
    .line {
      background: white;
      width: 100%;
      height: 2px;
      border-radius: 10px;
    }
    .dot {
      width: 10px;
      height: 10px;
      background: white;
      border-radius: 50%;
      position: absolute;
      top: 0;
      left: 0;
      margin-left: -5px;
      z-index: 0;
      box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.3);
    }
  }
  .icons {
    margin: 40px 0;
    display: flex;
    justify-content: space-around;
    .p {
      .play,
      .pause {
        position: relative;
        display: block;
        width: 30px;
        height: 30px;
        margin-top: 0px;
        border-radius: 50%;
        border: 1px solid white;
      }
      .play {
        &::before {
          position: absolute;
          top: 50%;
          left: 35%;
          margin-top: -7px;
          content: "";
          width: 20px;
          height: 10px;
          display: block;
          border-top: 8px solid transparent;
          border-right: 10px solid transparent;
          border-bottom: 8px solid transparent;
          border-left: 8px solid white;
          margin-left: 2px;
        }
      }
      .pause {
        &::before {
          position: absolute;
          top: 50%;
          left: 35%;
          margin-top: -5px;
          content: "";
          width: 10px;
          height: 10px;
          display: block;
          background: white;
        }
      }
    }
    img {
      width: 35px;
      height: 35px;
    }
  }
}
