























.play-header{
  margin: 10px;
  display: flex;
  .play-header-back{
    flex: 8%;
    
  }
  .play-song{
    flex: 92%;
    display: inline-block;
    text-align: center;
    .play-header-name{
      display: block;
      font-size: 16px;
    }
    .singer{
      font-size: 14px;
      color: #b2b2b2;
      &::after {
          content: "/";
          margin: 0 2px;
        }
      &:last-of-type {
        &::after {
          content: "";
        }
      }
    }
  }
  span{
  color: white;
}
}
