.m-history .lists {
  display: flex;
  align-items: center;
  height: 45px;
  -webkit-box-align: center;
}
.m-history .lists .u-svg {
  display: inline-block;
  margin: 0 10px;
  width: 15px;
  height: 15px;
  background-image: url(../assets/7.svg);
  vertical-align: middle;
  background-position: 0 0;
  background-size: contain;
  background-repeat: no-repeat;
}
.m-history .lists .histyr {
  flex: 1;
  width: 1%;
  display: flex;
  -webkit-box-align: center;
  align-items: center;
  height: 45px;
  position: relative;
}
.m-history .lists .histyr::after {
  position: absolute;
  z-index: 2;
  content: "";
  top: 0;
  left: 0;
  pointer-events: none;
  box-sizing: border-box;
  transform-origin: top left;
  border: 0 solid rgba(0, 0, 0, 0.1);
  border-bottom-width: 1px;
  width: 200%;
  height: 200%;
  transform: scale(0.5);
}
.m-history .link {
  flex: 1;
  width: 1%;
  margin-right: 10px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-break: normal;
}
.m-history .close {
  flex: 0 0 auto;
  width: 32px;
  height: 32px;
  line-height: 32px;
}
.m-history .close .u-svg-close {
  margin-left: 2px;
  width: 12px;
  height: 12px;
  background-image: url(../assets/8.svg);
  display: inline-block;
  vertical-align: middle;
  background-position: 0 0;
  background-size: contain;
  background-repeat: no-repeat;
}
