.play-header {
  margin: 10px;
  display: flex;
}
.play-header .play-header-back {
  flex: 8%;
}
.play-header .play-song {
  flex: 92%;
  display: inline-block;
  text-align: center;
}
.play-header .play-song .play-header-name {
  display: block;
  font-size: 16px;
}
.play-header .play-song .singer {
  font-size: 14px;
  color: #b2b2b2;
}
.play-header .play-song .singer::after {
  content: "/";
  margin: 0 2px;
}
.play-header .play-song .singer:last-of-type::after {
  content: "";
}
.play-header span {
  color: white;
}
