




















@keyframes disc-rotate {
  from{
    transform: rotate(0deg);
  }
  to{
    transform: rotate(360deg);
  }
}
.disc{
  margin: 150px 100px ;
  img{
    border-radius: 50%;
    animation: disc-rotate 10s linear infinite;
  }
  &.paused{
    img{
      animation-play-state: paused;
    }
  }
}
