.current-play-list-item {
  margin: 0 15px;
  display: flex;
  justify-content: space-between;
}
@keyframes shensuo {
  from {
    height: 20%;
  }
  to {
    height: 100%;
  }
}
.current-play-list-item .playing-animate {
  width: 15px;
  height: 15px;
  display: flex;
  justify-content: space-around;
  align-items: flex-end;
  margin-right: 5px;
}
.current-play-list-item .playing-animate span {
  width: 15%;
  height: 20%;
  background: red;
  animation: shensuo 0.6s linear infinite alternate;
}
.current-play-list-item .playing-animate span:nth-child(1) {
  animation-delay: 0s;
}
.current-play-list-item .playing-animate span:nth-child(2) {
  animation-delay: -0.2s;
}
.current-play-list-item .playing-animate span:nth-child(3) {
  animation-delay: -0.4s;
}
.current-play-list-item .playing-animate.paused span {
  animation-play-state: paused;
}
.current-play-list-item .detail {
  margin-bottom: 10px;
  flex: 1;
  font-size: 16px;
}
.current-play-list-item .detail .artist {
  font-size: 10px;
  color: #999;
}
.current-play-list-item .detail .artist::after {
  content: "/";
}
.current-play-list-item .detail .artist:last-of-type::after {
  content: "";
}
.current-play-list-item .delete {
  background: url(../assets/2.png);
  width: 20px;
  height: 20px;
  background-size: 20px 20px;
}
