


















































.nimate__animated{
  animation-duration: 0.3s;
}
.current-playlist {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  padding-bottom: 5%;
  .mask {
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
  }
  .card {
    width: 90%;
    min-height: 230px;
    max-height: 70vh;
    background: white;
    border-radius: 10px;
    h3 {
      margin: 10px;
    }
  }
}
