p {
  height: 20px;
  line-height: 20px;
  background: #dfe4e4;
  font-size: 10px;
  padding-left: 20px;
}
.list-header {
  position: relative;
  width: 100%;
  height: 180px;
  background: url(${item.coverImgUrl}) rgba(0, 0, 0, 0.25);
  color: aliceblue;
}
.list-header .text {
  position: absolute;
  top: 30px;
  left: 20px;
  display: flex;
}
.list-header .text .mask {
  position: absolute;
  top: 0;
  left: 0;
  width: 90%;
  height: 100%;
  background-size: cover;
  background-position: center;
  filter: blur(7px) brightness(0.9);
  transform: scale(1.5);
  z-index: -1;
}
.list-header .text .plhead_fl {
  width: 50%;
  height: 100%;
  position: relative;
}
.list-header .text .plhead_fl::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 18px;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.2);
}
.list-header .text .plhead_fl .lsthd_icon {
  position: absolute;
  z-index: 3;
  top: 10px;
  left: 0;
  padding: 0 8px;
  height: 17px;
  color: #fff;
  font-size: 9px;
  text-align: center;
  line-height: 17px;
  background-color: rgba(217, 48, 48, 0.8);
  border-top-right-radius: 17px;
  border-bottom-right-radius: 17px;
}
.list-header .text .plhead_fl i {
  position: absolute;
  top: 5px;
  right: 0;
  color: white;
  font-size: 12px;
  line-height: 1em;
  padding: 0 10px;
}
.list-header .text .plhead_fl i::before {
  content: "";
  width: 12px;
  height: 11px;
  display: inline-block;
  background: url("data:image/svg+xml;base64,");
  background-repeat: no-repeat;
  vertical-align: bottom;
  margin-right: 4px;
}
.right {
  margin-left: 20px;
  font-size: 18px;
}
