.comment {
  position: relative;
}
.comment .comment-icon {
  position: absolute;
  width: 30px;
  height: 30px;
  margin: 10px;
}
.comment .comment-icon img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.comment .comment-right {
  margin-left: 50px;
  border-bottom: 1px solid #eee;
  padding: 10px 0;
}
.comment .comment-right h5 {
  font-size: 14px;
}
.comment .comment-right .comment-time {
  font-size: 9px;
  display: block;
  color: #999;
  margin: 5px 0;
}
.comment .comment-right .comment-text {
  font-size: 14px;
  line-height: 22px;
}
