.playbar {
  width: 100%;
  height: 50px;
  background: white;
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  padding: 5px 10px;
  align-items: center;
  box-shadow: 0 -1px 3px 0 rgba(0, 0, 0, 0.2);
}
.playbar img {
  border-radius: 50%;
  width: 40px;
  height: 40px;
}
.playbar h5 {
  flex: 1;
  padding: 0 10px;
  font-size: 14px;
}
.playbar .progress {
  position: relative;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.playbar .progress span.play-icon {
  width: 10px;
  height: 10px;
  overflow: hidden;
}
.playbar .progress span.play-icon::before {
  content: "";
  width: 20px;
  height: 10px;
  display: block;
  border-top: 5px solid transparent;
  border-right: 12px solid transparent;
  border-bottom: 5px solid transparent;
  border-left: 8px solid #bbb;
  margin-left: 2px;
}
.playbar .progress span.paused-icon {
  width: 8px;
  height: 8px;
  overflow: hidden;
}
.playbar .progress span.paused-icon::before {
  content: "";
  width: 100%;
  height: 100%;
  display: block;
  background: #bbb;
}
.playbar .progress canvas {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
}
.playbar .currentlist {
  background: url("../assets/leibiao.png") no-repeat;
  background-size: 100%;
  width: 20px;
  height: 20px;
  margin-left: 15px;
}
