




































li.newest-item {
  display: flex;
  align-items: center;
 
  .index {
    margin-left: 10px;
  }
  &:nth-child(1),
  &:nth-child(2),
  &:nth-child(3) {
    .index {
      color: red;
    }
  }
  .info{
    flex: 1;
    display: flex;
   padding: 10px 0;
  border-bottom: 1px solid lightgray;
  padding-right: 10px;
  margin-left: 10px;
     .detail {
    flex: 1;
    h3 {
    }
    p {
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden;
      font-size: 12px;
      color: #888;
      .artist {
        &::after {
          content: "/";
        }
      }
      span {
        &:last-of-type {
          &::after {
            content: "";
          }
        }
      }
      i {
      }
    }
  }
  }
  .icon {
    width: 22px;
    height: 22px;
    background-image: url("../assets/index_icon_2x.png");
    background-size: 166px 97px;
    background-position: -24px 0;
  }
}
